import SimpleLightbox from "simplelightbox";
// Додатковий імпорт стилів
import "simplelightbox/dist/simple-lightbox.min.css";

 export function createMarkUp(images) {
    const photoList = document.createElement('ul');
    photoList.classList.add('photo-list');
    
    const markup = images.map(({ webformatURL, largeImageURL, tags, likes, views, comments, downloads }) =>
        `
     <li class="wrap-card">
       <a class"link-card" href="${largeImageURL}">
         <img class="photo-card" src="${webformatURL}" alt="${tags}"/>
      </a> 

          <div class="info">
          <div class = "wrap-text">
          <p>
             <b class"text">Likes</b> ${likes}
           </p> 
          </div>
           <div class = "wrap-text">
           <p class"text">
             <b>Views</b> ${views}
           </p>
           
           </div>
           
           <div class = "wrap-text-two">
           <p class"text">
             <b>Comments</b>${comments}
           </p>
           
           </div>

           <div class = "wrap-text-two">
           <p class"text">
             <b>Downloads</b>${downloads}
           </p>
           
           </div>
           
         </div>
    </li>
   `).join('');
    
     photoList.innerHTML = markup;
     
     document.getElementById('gallery').append(photoList);    
     
     
const lightbox =
    new SimpleLightbox('#gallery a', {
        captionsData: 'alt',
        captionDelay: 250,
       });

lightbox.refresh();

}


